export default {
  head() {
    return {
      title: this.home.title
    }
  },
  async asyncData({ params, $dataApi }) {
    const {
      data,
      status,
      statusText,
      ok
    } = await $dataApi.getHome(params.id);

    if (! ok) {
      return error({
        statusCode: status,
        message: statusText
      })
    }

    return {
      home: data,
    }
  },
  mounted() {
    this.$maps.showMap(this.$refs.map, this.home._geoloc.lat, this.home._geoloc.lng);
  },
  methods: {

  }
}
